import React, { lazy, Suspense } from "react";
import { IconButton, Badge } from "@material-ui/core";
import { ShoppingCartOutlined } from "@material-ui/icons";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { SearchForm } from "../SearchForm";
const NoticficationButton = lazy(() => import("../NoticficationButton"));
export const Header = (props) => {
    const userStatus = useSelector((state) => state.userStatus);
    return (
        <header className={`${props.addStyle} w-100`}>
            <div className=" d-flex justify-content-be align-content-center align-items-center header mx-auto p-2">
                <Link to="/" className="logo">
                    VietCourses
                </Link>
                <div className="dropdown subject-dropdown">
                    <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown">
                        Danh mục
                    </button>
                    <div className="dropdown-menu">
                        <Link to="/linhvuc" className="dropdown-item">
                            Nghiên cứu khoa học
                        </Link>
                        <Link to="linhvuc/cntt" className="dropdown-item">
                            Công nghệ thông tin
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Thiết kế đồ họa
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Marketing-Truyền thông
                        </Link>
                        <Link to="/linhvuc/kinhdoanh" className="dropdown-item">
                            Kinh doanh
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Ngoại ngữ
                        </Link>
                        <Link to="/linhvuc/amnhac" className="dropdown-item">
                            Âm nhạc
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Khoa học dữ liệu
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Khoa học máy tính
                        </Link>
                        <Link to="/linhvuc" className="dropdown-item">
                            Sức khỏe
                        </Link>
                    </div>
                </div>
                <SearchForm />
                <Suspense fallback={<div></div>}>
                    {userStatus.userInfo ? (
                        <>
                            <NoticficationButton />
                            <Link to="/cart">
                                <IconButton aria-label="cart">
                                    <Badge
                                        badgeContent={userStatus.userInfo.cart.length}
                                        color="secondary"
                                        anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
                                    >
                                        <ShoppingCartOutlined />
                                    </Badge>
                                </IconButton>
                            </Link>
                        </>
                    ) : (
                        ""
                    )}
                </Suspense>
            </div>
        </header>
    );
};
